.browser{
  width:100%;
  border-radius:.5rem;
  border:1px solid #f9f9f9;
  box-shadow:0 1rem 1rem rgba(0,0,0,0.1);
  &-buttons{
    padding:0 .25rem;
    display:flex;
    flex-direction:row;
    align-items:center;
    margin-right:.25rem;
    i {
      height:1rem;
      width:1rem;
      border-radius: 100%;
      display:block;
      background-color:var(--icon-color);
      &:nth-child(1){--icon-color:#ff6052;}
      &:nth-child(2){--icon-color:#ffb933;}
      &:nth-child(3){--icon-color:#2ec733;}
    }
    i + i {
      margin-left:.25rem;
    }
  }
  &-console{
    flex:1;
    border-radius:.4rem;
    height:2rem;
    background-color:#fff;
    display:flex;
    flex-direction:row;
    align-items:center;
    padding:0 .75rem;
    font-size:0.875rem;
    color:#646464;
    min-width:0;
    white-space: nowrap;
    overflow:hidden;
  }
  &-toolbar{
    padding:0 .25rem;
    border-radius:.5rem .5rem 0 0;
    background: #f9f9f9;
    height:2.5rem;
    display:flex;
    flex-direction:row;
    align-items:center;
  }
  &-window{
    position:relative;
    padding-bottom:56%;
    border-radius: 0 0 .5rem .5rem;
    overflow-y:auto;
  }
  &-document{
    position:absolute;
    top:0;left:0;
    width:100%;
    height:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding:var(--gap);
  }
  transition: transform 0.6s ease-in-out;
}

.scroll{
  .browser{
    transform: perspective(64rem) rotateX(18deg);
  }
}